<template>
  <div class="content">
  {{error}}

    <div class="md-layout">
      <div class="md-layout-item md-medium-size-33 md-xsmall-size-100 md-size-33">
        <stats-card data-background-color="green">
          <template slot="header">
            <md-icon>attach_money</md-icon>
          </template>
          <template slot="content">
            <p class="category">1 BTC</p>
            <h3 class="title">${{mercado}}</h3>
          </template>
          <template slot="footer">
            <div class="stats">
              Cotización de Mercado
            </div>
          </template>
        </stats-card>
      </div>
      <div class="md-layout-item md-medium-size-33 md-xsmall-size-100 md-size-33">
        <stats-card data-background-color="orange">
          <template slot="header">
            <md-icon>attach_money</md-icon>
          </template>
          <template slot="content">
            <p class="category">1 BTC</p>
            <h3 class="title">${{((mercado*(100+parseFloat(porcentaje)))/100).toFixed(2)}}</h3>
          </template>
          <template slot="footer">
            <div class="stats">
              Valor BTC + {{porcentaje}}%
            </div>
          </template>
        </stats-card>
      </div>
      <div class="md-layout-item md-medium-size-33 md-xsmall-size-100 md-size-33">
        <stats-card data-background-color="blue">
          <template slot="header">
            <md-icon>monetization_on</md-icon>
          </template>
          <template slot="content">
            <p class="category">Cuenta Maestra BTC</p>
            <h3 class="title">{{balance}}</h3>
          </template>
          <template slot="footer">
            <div class="stats">
              Cuenta Maestra ${{(balance*((mercado*(100+parseFloat(porcentaje)))/100)).toFixed(2)}}
            </div>
          </template>
        </stats-card>
      </div>
    </div>

    <div class="md-layout">
      <div class="md-layout-item md-medium-size-100 md-size-100">  
        <md-card>
          <md-card-header data-background-color="blue">
            <h4 class="title">Configuración para Transacciones BTC</h4>
          </md-card-header>
          <md-card-content>
            <div class="md-layout">
              <div class="md-layout-item md-small-size-100 md-size-40">
                <md-field>
                  <label>Servicios: Address</label>
                  <md-input v-model="address.servicio" type="text" 
                  :disabled="disabled == true"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-40">
                <md-field>
                  <label>Comisión de Uso: Address</label>
                  <md-input v-model="address.comision" type="text"
                  :disabled="disabled == true"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-20">
                <md-field>
                  <label>% Comisión</label>
                  <md-input v-model="comision" type="number" step="0.01"
                  min="0" max="100" :disabled="disabled == true"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-100">
                <p>Proveedor de Liquidez</p>     
              </div>
              <div class="md-layout-item md-small-size-100 md-size-40">
                <md-field>
                  <label>Address</label>
                  <md-input v-model="address.proveedor" type="text"
                  :disabled="disabled == true"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-40">
                <md-field>
                  <label>Private Key (Wallet Import Format)</label>
                  <md-input v-model="WIF" type="password"
                  :disabled="disabled == true"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-20">
                <md-field>
                  <label>% Comisión</label>
                  <md-input v-model="comision" type="number" step="0.01"
                  min="0" max="100" :disabled="disabled == true"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-size-60 text-right"></div>
              <div class="md-layout-item md-size-20 text-right" v-if="disabled"></div>
              <div class="md-layout-item md-size-20 text-right" v-if="disabled">
                <md-list-item @click="Editar()">
                  <div class="stats">
                    <md-icon>edit</md-icon>Editar
                  </div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-size-20 text-right" v-if="!disabled">
                <md-list-item @click="Guardar()">
                  <div class="stats">
                    <md-icon>save</md-icon>Guardar
                  </div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-size-20 text-right" v-if="!disabled">
                <md-list-item @click="Cerrar()">
                  <div class="stats">
                    <md-icon>close</md-icon>Cerrar
                  </div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-20">
                <md-list-item @click="Ver(1)">
                  <div class="stats">
                    <md-icon>visibility</md-icon>Uthos
                  </div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-40">
                <md-list-item @click="Ver(2)">
                  <div class="stats">
                    <md-icon>visibility</md-icon>Transacciones
                  </div>
                </md-list-item>
              </div>
            </div>
          </md-card-content>
        </md-card>   
      </div>
    </div>

    <div class="md-layout-item md-medium-size-100 md-size-100" v-if="ver == 1">   
      <md-card>
        <md-card-header data-background-color="orange">
          <h4 class="title">Utxos</h4>
          <p class="category">Satoshis: {{utxos_disp}}</p>
        </md-card-header>
        <md-card-content>
          <md-table v-model="utxos" >
            <md-table-row slot="md-table-row" slot-scope="{ item }">
              <md-table-cell md-label="Hash">{{ item.txid }}</md-table-cell>
              <md-table-cell md-label="Satoshis">{{ item.satoshis }}</md-table-cell>
              <md-table-cell md-label="Confirmed">{{ item.confirmed }}</md-table-cell>
            </md-table-row>
          </md-table>           
        </md-card-content>
      </md-card>
    </div>

    <div class="md-layout-item md-medium-size-100 md-size-100" v-if="ver == 2">   
      <md-card>
        <md-card-header data-background-color="orange">
          <h4 class="title">Transacciones</h4>
          <p class="category">Cantidad de Transacciones: {{movimientos.length}}</p>
        </md-card-header>
        <md-card-content>
          <md-table v-model="movimientos" >
            <md-table-row slot="md-table-row" slot-scope="{ item }">
              <md-table-cell md-label="Hash">{{ item.hash }}</md-table-cell>
              <md-table-cell md-label="Satoshis">{{ item.satoshis }}</md-table-cell>
              <md-table-cell md-label="Confirmed">{{ item.confirmed }}</md-table-cell>
            </md-table-row>
          </md-table>           
        </md-card-content>
      </md-card>
    </div>

  </div>
</template>

<script>
import Security from "./Layout/Security";
import API from "./Layout/GlobalAPI";
import Mensaje from "../globalNotificaciones";
import axios from "axios";
import CoinKey from "coinkey";
import ci from "coininfo";
import * as secure from "securejs";
import BT from "../Bitcoin";
const Bitcoin = new BT('testnet');

import { StatsCard } from "@/components";

export default {
  components: {
    StatsCard
  },

  data() {
    return {
      error:    null,
      mercado: 0,
      porcentaje: 0,
      comision: 0,
      wallet: null,
      balance: 0,
      WIF: null,
      address: {
        proveedor:  "-",
        comision:   "-",
        servicio:   "-"
      },
      Hex: null,
      disabled: true,
      hash: null,
      valor: null,
      transactions: 0,
      ver: 0,
      utxos: [],
      utxos_disp: 0,
      movimientos: [],

    };
  },
  created () { 
    Security(3);
    this.Mostrar();
  },
  computed: {
  },
  methods: {
    Mostrar() {
      this.error =  null;
      this.wallet =  null;
      this.balance =  0;
      this.porcentaje =  0;
      this.comision =  0;
      this.WIF =  null;
      this.address =  {};
      this.Hex =  null;
      this.disabled =  true;
      this.hash = null;
      this.valor = null;
      this.utxos = [];
      this.movimientos = [];
      this.transactions = 0;
      this.ver = 0;
      this.utxos_disp = 0;
      API({
        action:  'configuracion',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password')
      }).then(response => {
        this.porcentaje = response.data.configuracion.price_spread;
        this.comision = response.data.configuracion.comision;
        const decWIF = secure.decrypt(response.data.configuracion.proveedor_wif, 'btcoin');
        this.WIF = decWIF;
        this.address.proveedor = response.data.configuracion.proveedor_address;
        this.address.comision = response.data.configuracion.comision_address;
        this.address.servicio = response.data.configuracion.servicio_address;
        Bitcoin.privateKeyToAccount(decWIF);
        this.wallet = Bitcoin.defaultAccount.address;
        Bitcoin.currentprice().then(result => {
          const btc = parseFloat((result.bpi.USD.rate).replace(',', '')).toFixed(2)
          this.mercado  = btc;
        });
        Bitcoin.getBalance().then(result => {
          this.transactions = result.chain_stats['tx_count']+ result.mempool_stats['tx_count'];
          const received = result.chain_stats['funded_txo_sum'];
          const spent = result.chain_stats['spent_txo_sum'];
          this.balance = ((received-spent)/100000000).toFixed(8);
        });
        Bitcoin.getUtxo().then(result => {
          this.utxos = result;
          let disp = 0;
          for (let i = 0; i < result.length; i++) {
            if (result[i]['confirmed']) {
              disp += result[i]['satoshis'];
            }
          }
          this.utxos_disp = disp;
        });
        Bitcoin.getTxs().then(result => {
          result.forEach(element => {
            let value = 0;
            for(let i = 0; i < element['vout'].length; i++){
              value = value + element['vout'][i]['value'];
            }
            this.movimientos.push({
              hash: element['txid'],
              satoshis: (value/100000000).toFixed(8) + ' BTC',
              confirmed: element["status"]["confirmed"],
            });
          });
        });
      });
    },
    Editar() {
      this.disabled = false;
    },
    Guardar() {
      if (!Bitcoin.isValidAddress(this.address.servicio)){
        this.$notify(Mensaje(3,'Address de Servicio Inválida.'));
      } else if (!Bitcoin.isValidAddress(this.address.comision)){
        this.$notify(Mensaje(3,'Address de Servicio Inválida.'));
      } else if (!Bitcoin.isValidAddress(this.address.proveedor)){
        this.$notify(Mensaje(3,'Address de Proveedor Inválida.'));
      } else if (this.address.proveedor != this.wallet){
        this.$notify(Mensaje(3,'Private Key de Proveedor Inválida'));
      } else {
        API({
          action:  'updateconfiguracion',
          correo:   localStorage.getItem('Login'),
          clave:    localStorage.getItem('Password'),
          price_spread: this.porcentaje,
          comision: this.comision,
          proveedor_address: this.address.proveedor,
          proveedor_wif: secure.encrypt(this.WIF, 'btcoin'),
          servicio_address: this.address.servicio,
          comision_address: this.address.comision
        }).then(response => {
          if (response.data.error){
            this.$notify(Mensaje(3,response.data.message));
            this.Mostrar();
          } else {
            this.$notify(Mensaje(2,response.data.message));
          }   
        });
        this.disabled = true;
      }

    },
    Cerrar() {
      this.disabled = true;
    },
    Ver(caso) {
      if (this.ver == caso) {
        this.ver = 0;
      } else {
        this.ver = caso;
      }
    },
    Generar() {
      //const ck = new CoinKey.createRandom();
      //TESNET
      const ck = CoinKey.createRandom(ci('BTC-TEST'))
      this.wallet = ck.publicAddress;
      this.WIF = ck.privateWif;
      this.Hex = ck.privateKey.toString('hex');
    },
  }
};
</script>